/* Verstige Glamorism Design System */

@layer utilities {

    /* Glass Morphism */
    .glass-glamor {
        background: rgba(255, 255, 255, 0.03);
        backdrop-filter: blur(20px);
        -webkit-backdrop-filter: blur(20px);
        border: 1px solid rgba(255, 255, 255, 0.1);
    }

    .glass-glamor-heavy {
        background: rgba(255, 255, 255, 0.05);
        backdrop-filter: blur(30px);
        -webkit-backdrop-filter: blur(30px);
        border: 1px solid rgba(255, 255, 255, 0.15);
    }

    /* Neon Glows */
    .glow-profit {
        box-shadow: 0 0 20px rgba(16, 185, 129, 0.4);
    }

    .glow-profit-strong {
        box-shadow: 0 0 30px rgba(16, 185, 129, 0.6), 0 0 60px rgba(16, 185, 129, 0.3);
    }

    .glow-loss {
        box-shadow: 0 0 20px rgba(239, 68, 68, 0.4);
    }

    .glow-loss-strong {
        box-shadow: 0 0 30px rgba(239, 68, 68, 0.6), 0 0 60px rgba(239, 68, 68, 0.3);
    }

    .glow-accent {
        box-shadow: 0 0 20px rgba(59, 130, 246, 0.4);
    }

    .glow-purple {
        box-shadow: 0 0 20px rgba(162, 89, 255, 0.4);
    }

    .glow-gold {
        box-shadow: 0 0 25px rgba(251, 191, 36, 0.5), 0 0 50px rgba(251, 191, 36, 0.2);
    }

    /* Neon Borders */
    .border-profit-neon {
        border-color: rgba(16, 185, 129, 0.5);
    }

    .border-loss-neon {
        border-color: rgba(239, 68, 68, 0.5);
    }

    .border-accent-neon {
        border-color: rgba(59, 130, 246, 0.5);
    }

    /* Pulse Animations */
    .pulse-profit {
        animation: pulse-profit 2s cubic-bezier(0.4, 0, 0.6, 1) infinite;
    }

    .pulse-loss {
        animation: pulse-loss 2s cubic-bezier(0.4, 0, 0.6, 1) infinite;
    }

    .pulse-data {
        animation: pulse-data 1.5s ease-in-out infinite;
    }

    @keyframes pulse-profit {

        0%,
        100% {
            box-shadow: 0 0 20px rgba(16, 185, 129, 0.4);
        }

        50% {
            box-shadow: 0 0 30px rgba(16, 185, 129, 0.6), 0 0 60px rgba(16, 185, 129, 0.3);
        }
    }

    @keyframes pulse-loss {

        0%,
        100% {
            box-shadow: 0 0 20px rgba(239, 68, 68, 0.4);
        }

        50% {
            box-shadow: 0 0 30px rgba(239, 68, 68, 0.6), 0 0 60px rgba(239, 68, 68, 0.3);
        }
    }

    @keyframes pulse-data {

        0%,
        100% {
            opacity: 1;
            transform: scale(1);
        }

        50% {
            opacity: 0.7;
            transform: scale(1.05);
        }
    }

    /* Gradient Backgrounds */
    .bg-gradient-profit {
        background: linear-gradient(135deg, rgba(16, 185, 129, 0.1) 0%, rgba(16, 185, 129, 0.05) 100%);
    }

    .bg-gradient-loss {
        background: linear-gradient(135deg, rgba(239, 68, 68, 0.1) 0%, rgba(239, 68, 68, 0.05) 100%);
    }

    .bg-gradient-gold {
        background: linear-gradient(135deg, #fbbf24 0%, #f59e0b 50%, #d97706 100%);
    }

    .bg-gradient-silver {
        background: linear-gradient(135deg, #e2e8f0 0%, #cbd5e1 50%, #94a3b8 100%);
    }

    .bg-gradient-bronze {
        background: linear-gradient(135deg, #f59e0b 0%, #d97706 50%, #b45309 100%);
    }

    /* Haptic Feedback */
    .haptic-feedback {
        transition: transform 0.1s ease-out;
    }

    .haptic-feedback:active {
        transform: scale(0.98);
    }

    /* Command Center Grid */
    .command-grid {
        display: grid;
        grid-template-columns: 300px 1fr 350px;
        gap: 20px;
        height: calc(100vh - 80px);
    }

    @media (max-width: 1536px) {
        .command-grid {
            grid-template-columns: 280px 1fr 320px;
        }
    }

    @media (max-width: 1280px) {
        .command-grid {
            grid-template-columns: 1fr;
            grid-template-rows: auto 1fr auto;
        }
    }

    /* Frosted Overlay */
    .frosted-overlay {
        background: rgba(0, 0, 0, 0.7);
        backdrop-filter: blur(10px);
        -webkit-backdrop-filter: blur(10px);
    }

    /* Execution Prison Lock */
    .prison-lock {
        position: relative;
        pointer-events: none;
        opacity: 0.7;
    }

    .prison-lock::after {
        content: '';
        position: absolute;
        inset: 0;
        background: repeating-linear-gradient(45deg,
                transparent,
                transparent 10px,
                rgba(239, 68, 68, 0.05) 10px,
                rgba(239, 68, 68, 0.05) 20px);
        pointer-events: none;
    }
}